styles\n//\n\n.btn {\n  display: inline-flex;\n  justify-content: center;\n  font-weight: $btn-font-weight;\n  @include font-smoothing;\n  border: 1px solid transparent;\n  border-radius: $btn-border-radius;\n  user-select: none;\n  @include button-size(\n    $btn-padding-y,\n    $btn-padding-x,\n    $btn-font-size-base,\n    $btn-line-height,\n    $btn-min-width-sm\n  );\n  transition: all 0.1s ease-in-out;\n}\n\n// Future-proof disabling of clicks on `<a>` elements\na.btn.disabled,\nfieldset[disabled] a.btn {\n  pointer-events: none;\n}\n\n@each $name, $color in $btn-swatches {\n  @if $name == \"yellow\" {\n    @include button-variants($name, $color-black, $color);\n  }\n  @elseif $name == \"brand-yellow\" {\n    @include button-variants($name, $color-black, $color);\n  }\n  @elseif lightness($color) > 68 {\n    @include button-variants($name, $color-black, $color);\n  } @else {\n    @include button-variants($name, $color-white, $color);\n  }\n}\n\n.round {\n  border-radius: $btn-round-border-radius;\n}\n\n.square {\n  border-radius: 0;\n}\n//\n// Button Sizes\n//\n\n.large {\n  // line-height: ensure even-numbered height of button next to large input\n  @include button-size(\n    $btn-padding-y-lg,\n    $btn-padding-x-lg,\n    $btn-font-size-lg,\n    $btn-line-height-lg,\n    $btn-min-width-lg\n  );\n}\n\n.small {\n  // line-height: ensure proper height of button next to small input\n  @include button-size(\n    $btn-padding-y-sm,\n    $btn-padding-x-sm,\n    $btn-font-size-sm,\n    $btn-line-height-sm,\n    $btn-min-width-sm\n  );\n}\n\n//\n// Block button\n//\n\n.block {\n  display: flex;\n  width: 100%;\n}\n\n.caps {\n  text-transform: uppercase;\n}\n\n.underline {\n  &:hover,\n  &:focus {\n    text-decoration: underline;\n  }\n}\n\n// Specificity overrides\ninput[type=\"submit\"],\ninput[type=\"reset\"],\ninput[type=\"button\"] {\n  &.block {\n    width: 100%;\n  }\n}\n\n// \"go\" animation styles\n\n@keyframes lantern-pulse {\n  0% {\n    background-color: rgba($deprecated-color-mint, 0.2);\n  }\n  100% {\n    background-color: rgba($deprecated-color-mint, 0.5);\n  }\n}\n\n.go {\n  // lantern\n  &.btn-darkmint {\n    background-color: rgba($deprecated-color-mint, 0.5);\n    animation: lantern-pulse 2s infinite ease;\n    animation-direction: alternate;\n\n    &:hover {\n      animation: none;\n    }\n  }\n}\n","@import \"~@codecademy/gamut-styles/utils\";\n\n$btn-padding-x: px-rem(16) !default;\n$btn-padding-y: 0.375rem !default;\n$btn-font-weight: bold !default;\n\n$btn-line-height: 1.5 !default;\n$btn-line-height-lg: 4 / 3 !default;\n$btn-line-height-sm: 1.5 !default;\n\n$btn-font-size-base: px-rem(16) !default;\n$btn-font-size-lg: px-rem(20) !default;\n$btn-font-size-sm: px-rem(16) !default;\n$btn-font-size-xs: 0.75rem !default;\n\n$btn-padding-x-sm: 0.75rem !default;\n$btn-padding-y-sm: 0.25rem !default;\n$btn-min-width-sm: px-rem(128) !default;\n\n$btn-padding-x-lg: 1.25rem !default;\n$btn-padding-y-lg: 0.75rem !default;\n$btn-min-width-lg: px-rem(160) !default;\n\n$btn-border-radius: 2px !default;\n$btn-round-border-radius: 50px !default;\n\n$btn-state-modifier: 20% !default;\n$btn-color-modifier: 10% !default;\n$btn-outline-hover-state-modifier: 0.9 !default;\n$btn-outline-active-state-modifier: 0.6 !default;\n$btn-box-shadow-focus-modifier: 0.5 !default;\n\n$btn-disabled-color: $deprecated-gamut-grey-800;\n$btn-box-shadow-color: rgba(0, 0, 0, 0.3);\n\n$btn-swatches: (\n  \"mint\": $deprecated-swatches-mint-700,\n  \"darkmint\": $deprecated-swatches-mint-800,\n  \"blue\": $deprecated-gamut-blue-500,\n  \"darkblue\": $deprecated-swatches-cc-blue-500,\n  \"midnightblue\": $deprecated-swatches-grey-blue-500,\n  \"grey\": $deprecated-swatches-grey-300,\n  \"red\": $deprecated-swatches-red-500,\n  \"yellow\": $deprecated-gamut-yellow-500,\n  \"greyblue\": $deprecated-swatches-grey-blue-600,\n  \"white\": $deprecated-color-white,\n  \"ccblue\": $deprecated-swatches-cc-blue-700,\n  \"royalblue\": $deprecated-gamut-royal-blue,\n  \"purple\": $deprecated-gamut-purple,\n  \"brand-red\": $brand-red,\n  \"brand-orange\": $brand-orange,\n  \"brand-yellow\": $brand-yellow,\n  \"brand-purple\": $brand-purple,\n  \"brand-pink\": $brand-pink,\n  \"brand-mint\": $brand-mint,\n  \"brand-beige\": $brand-beige,\n  \"brand-dark-blue\": $brand-dark-blue,\n  \"brand-blue\": $color-blue-500\n);\n","@mixin font-smoothing($value: on) {\n  @if $value == on {\n    -webkit-font-smoothing: antialiased;\n    -moz-osx-font-smoothing: grayscale;\n  } @else {\n    -webkit-font-smoothing: subpixel-antialiased;\n    -moz-osx-font-smoothing: auto;\n  }\n}\n","// Button variants\n//\n// Easily pump out default styles, as well as :hover, :focus, :active,\n// and disabled options for all buttons\n\n@mixin button-variant($color, $background, $border: transparent) {\n  $active-background: mix($color-black, $background, $btn-state-modifier);\n\n  @if $border == transparent {\n    $active-border: transparent;\n    $active-border-hover: transparent;\n  }\n\n  color: $color;\n  background-color: $background;\n  border-color: $border;\n\n  &:hover {\n    box-shadow: 0 2px 4px $btn-box-shadow-color;\n  }\n\n  &:focus {\n    box-shadow: 0 0 0 2px $color-white, 0 0 0 4px $background;\n  }\n\n  &:focus,\n  &:hover {\n    text-decoration: none;\n    color: $color;\n\n    &:active {\n      box-shadow: 0 2px 4px $btn-box-shadow-color;\n    }\n  }\n\n  &:active {\n    background-color: $active-background;\n  }\n\n  &:disabled {\n    background-color: $btn-disabled-color;\n\n    &:hover {\n      box-shadow: none;\n    }\n  }\n}\n\n@mixin button-outline-variant($color) {\n  $active-contrast-base: text-contrast-color($color);\n  $active-background: mix($active-contrast-base, $color, $btn-state-modifier);\n  color: $color;\n  background-color: transparent;\n  border: 1px solid $color;\n\n  &:focus,\n  &:hover {\n    background-color: $color;\n    box-shadow: none;\n    color: $active-contrast-base;\n\n    &:active {\n      background-color: $active-background;\n      box-shadow: none;\n    }\n  }\n\n  &:hover {\n    box-shadow: 0 2px 4px 0 $btn-box-shadow-color;\n  }\n\n  &:focus {\n    box-shadow: 0 0 0 2px $color-white, 0 0 0 4px $color;\n  }\n\n  &:disabled {\n    border-color: $btn-disabled-color;\n    color: $btn-disabled-color;\n    background-color: transparent;\n  }\n}\n\n@mixin button-flat-variant($color) {\n  color: $color;\n  background-color: transparent;\n\n  &:hover,\n  &:active {\n    box-shadow: none;\n  }\n\n  &:focus {\n    box-shadow: 0 0 0 2px $color-white, 0 0 0 4px $color;\n  }\n\n  &:disabled {\n    color: $btn-disabled-color;\n    background-color: transparent;\n  }\n}\n\n// Button sizes\n@mixin button-size(\n  $padding-y,\n  $padding-x,\n  $font-size,\n  $line-height,\n  $min-width\n) {\n  padding: $padding-y $padding-x;\n  font-size: $font-size;\n  line-height: $line-height;\n  min-width: $min-width;\n\n  &.fit-text {\n    min-width: 0;\n    min-height: 0;\n  }\n}\n\n@mixin button-variants($name, $color, $background, $border: transparent) {\n  .btn-#{$name} {\n    @include button-variant($color, $background, $border);\n    &.outline {\n      @include button-outline-variant($background);\n    }\n    &.flat {\n      @include button-flat-variant($background);\n    }\n    @content;\n  }\n  .link-#{$name} {\n    font-weight: bold;\n    @include font-smoothing;\n    color: $background;\n    text-decoration: underline;\n\n    &:disabled {\n      color: $btn-disabled-color;\n    }\n  }\n}\n\n// Shamelessly copied from https://codepen.io/davidhalford/pen/wlDxL\n// Adds text color to an element based on what has more contrast with its background\n\n@function color-brightness($color) {\n  @return round(\n    (red($color) * 299) + (green($color) * 587) + (blue($color) * 114) / 1000\n  );\n}\n\n$brightness-threshold: color-brightness($color-white) / 2;\n\n@function text-contrast-color($background) {\n  $brightness: color-brightness($background);\n\n  @if abs($brightness) < $brigh